import _isEmpty from 'lodash.isempty';

// getCityTaxRate
// params example:
//    price: 5500
//    rates:
//    [{
//     "ListPriceFrom": 0,
//     "ListPriceTo": 1500000,
//     "CityTaxPer": 0.015
//    },
//    {
//     "ListPriceFrom": 1500000,
//     "ListPriceTo": 9999999999,
//     "CityTaxPer": 0.025
//    }]
export function getCityTaxRate(params: any[]): number {
	if (_isEmpty(params)) return 0;
	const [price, rates] = params;
	if (!rates || !price) return 0;

	const rate = rates.find(
		({
			ListPriceFrom,
			ListPriceTo,
		}: {
			ListPriceFrom: number;
			ListPriceTo: number;
		}) => ListPriceFrom <= price && ListPriceTo > price
	);

	const rateValue = rate?.CityTaxPer || 0;
	return rateValue * price;
}
